class CharImg {
  constructor(img, sX, sY, sWidth, sHeight, width, height) {
    this.img = img;
    this.sX = sX;
    this.sY = sY;
    this.sWidth = sWidth;
    this.sHeight = sHeight;
    this.width = width;
    this.height = height;
  }
}

export default CharImg;



